<template>
    <div>
        <a @click.prevent="show" class="modal-btn">
          <i>
          <svg class="bi bi-bucket-fill" width="1em" height="1em" viewBox="0 0 16 16" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
            <path fill-rule="evenodd" d="M8 1.5A4.5 4.5 0 003.5 6h-1a5.5 5.5 0 1111 0h-1A4.5 4.5 0 008 1.5z" clip-rule="evenodd"/>
            <path fill-rule="evenodd" d="M1.61 5.687A.5.5 0 012 5.5h12a.5.5 0 01.488.608l-1.826 8.217a1.5 1.5 0 01-1.464 1.175H4.802a1.5 1.5 0 01-1.464-1.175L1.512 6.108a.5.5 0 01.098-.42z" clip-rule="evenodd"/>
          </svg>
        </i>
        </a>
        <modal name="modal-login">
            
            <div class="close-modal">
              <button @click="$modal.hide('modal-login')">
                <svg class="bi bi-x-octagon" width="1em" height="1em" viewBox="0 0 16 16" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                  <path fill-rule="evenodd" d="M4.54.146A.5.5 0 014.893 0h6.214a.5.5 0 01.353.146l4.394 4.394a.5.5 0 01.146.353v6.214a.5.5 0 01-.146.353l-4.394 4.394a.5.5 0 01-.353.146H4.893a.5.5 0 01-.353-.146L.146 11.46A.5.5 0 010 11.107V4.893a.5.5 0 01.146-.353L4.54.146zM5.1 1L1 5.1v5.8L5.1 15h5.8l4.1-4.1V5.1L10.9 1H5.1z" clip-rule="evenodd"/>
                  <path fill-rule="evenodd" d="M11.854 4.146a.5.5 0 010 .708l-7 7a.5.5 0 01-.708-.708l7-7a.5.5 0 01.708 0z" clip-rule="evenodd"/>
                  <path fill-rule="evenodd" d="M4.146 4.146a.5.5 0 000 .708l7 7a.5.5 0 00.708-.708l-7-7a.5.5 0 00-.708 0z" clip-rule="evenodd"/>
                </svg>
              </button>
            </div>
            <div v-if="!isCheckoutSection">
            <div class="box" v-for="post in posts" :key="post.id">
                <ul class="modal-post">
                  <li class="modal-image">
                    <img :src="post.image" alt="">
                  </li>
                  <li class="modal-information">
                    <p>{{ post.name }}</p>
                    <p>{{ post.costs }}</p>
                  </li>
                  <li class="modal-remove">
                    <button class="btn btn-danger" @click="removeFromCart(post.id)">Remove</button>
                  </li>
                </ul>
              </div>
            </div>
            <div v-if="isCheckoutSection">
              <p>{{ cartEmptyLabel }}</p>  
            </div>
        </modal>
    </div>
</template>
<script>

export default {
  data() {
    return {
      cartEmptyLabel: 'Your cart is empty',
      isCheckoutSection: false,
    }
  },
  methods: {
    show () {
      this.$modal.show('modal-login');
    },
    hide () {
      this.$modal.hide('modal-login');

    },
    
    removeFromCart (id) {
			let data = {
					id: id,
					status: false
			}
			this.$store.commit('removeFromCart', id);
			this.$store.commit('setAddedBtn', data);
    },
  },
  computed: {
    posts() {
			return this.$store.getters.postsAdded;
		},
  }
    
}
</script>

<style scoped>
  .close-modal button {
    background: #ffffff;
    border: none;
    float: right;
    margin-top: 0px;
    margin-right: 3px;
    font-size: 24px;
  }
  .modal-btn i {
    font-size: 24px;
  }
  .modal-image img {
    width: 80px;
  }
  .modal-post {
    display: flex;
    margin-top: 25px;
  }
  .modal-post li {
    display: block;
  }
  .modal-information {
    margin-left: 30px;
    padding-right: 80px;
  }
  .modal-remove button{
    margin-left: 100px;
  }
</style>