<template>
    <div class="container">
        <ul class="allposts">
            <li v-for="(post, index) in allPosts" :key="index">
                <router-link :key="post.id" :to="{ name: 'Add', params: { id: post.id }, }" class="shoes-side">
                    <div class="shoes">
                        <figure class="image">
                            <img :src="post.image" alt="">
                        </figure>
                        <a class="post-cost">
                            <p>{{ post.name }} </p>
                        </a>
                        <div class="posts">
                            <h5>{{ post.costs }}</h5>
                        </div>    
                    </div>
                </router-link>

            </li>
        </ul>
    </div>
</template>
<script>
import {mapGetters} from 'vuex'
export default {
    data() {
        return {
            posts: []
        }
    },
    computed: mapGetters(['allPosts']),
    async mounted() {
        this.$store.dispatch("fetchPosts")
    },
    
}
</script>
<style scoped>
    .allposts {
        padding-left: 0px;
        margin-top: 40px;
    }
    .allposts li {
        display: inline;
    }
    .image img {
        width: 300px;
        height: 340px;
    }
    .shoes-side {
        display: inline-flex;
    }
    .shoes {
        margin-left: 55px;
    }
    .shoes a {
        display: flex;
    }
    .post-cost h5 {
        margin-left: 80px;
        position:relative;
        right: 0;
    }
    .post-cost {
        text-decoration: none;
        display: inline-flex;
        color: black;
    }
    .posts {
        margin-top: -42px;
        float: right;
    }
    
    
</style>