<template>
    <div class="container">
        <ul v-for="(post, index) in allPosts" :key="index">
            <div v-if="postId == post.id" class="addside">
                <li class="col-4 addphoto" :key="index">
                    <img :src="post.image" alt="">
                </li>
                <li class="col-4 addphoto">
                    <img :src="post.front" alt="">
                </li>
                <li class="col-4 addinformation">
                    <p>{{ post.name }}</p>
                    <h5>{{ post.costs }}</h5>
                    <button v-if="isAddedBtn" @click="addToCart(post.id)" type="button" class="btn btn-success">Add to Cart</button>
                </li>
            </div>
        </ul>
    </div>
</template>

<script> 
import {mapGetters} from 'vuex'
export default {
    data() {
        return {
            postId: this.$route.params.id,
    
            
        }
    },
    computed: mapGetters(['allPosts']),     
    async mounted() {
        this.$store.dispatch("fetchPosts");
        this.$store.dispatch("addToCart")
    },
    methods: {
        addToCart (id) {
            let data = {
                id: id,
                status: true
            }
            this.$store.commit('addToCart', id);
            this.$store.commit('setAddedBtn', data);
        },
        isAddedBtn () {
        return this.product.isAddedBtn;
        },
    }
    
}
</script>

<style scoped>
    .addside li {
        display: inline-block;
    }
    .addphoto img {
        width: 320px;
    }
</style>